.news-modal {
	display: none;

	position: relative;
	width: 758px;

	background-color: transparent;
	color: #000;

	@include tablet {
		width: 738px;
	}

	@include mobile {
		max-width: calc(100% - 30px);
    
	}
}

.news-modal__body {
  position: relative;
  padding: 40px;
  width: 100%;
  border-radius: var(--radius);
  background-color: #fff;
  color: #2f2f2f;
  transition: all 0.2s ease-in;

  @include mobile {
    max-height: 500px;
    overflow-y: scroll;
  }
}

.news-modal__title {
  margin-bottom: 20px;

  font-family: var(--second-family);
  font-weight: 400;
  font-size: 32px;
  line-height: 100%;
  color: var(--primary-dark);

  @include tablet {
    font-size: 24px;
    line-height: 110%;
  }

  @include mobile {
    font-size: 16px;
  }
}
.news-modal__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 15px;

  @include tablet {
    
  }
}
.news-modal__close {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 32px;
  height: 32px;
  border-radius: 4px;

  transition: box-shadow .3s ease-in-out;

  &::after {
    content: "";

    position: absolute;
    top: 50%;
    left: 50%;
    width: 18px;
    height: 18px;

    transform: translate(-50%, -50%);

    background-image: url('../img/icons/nav-btn-close.svg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }

  &:hover {
    box-shadow: 0px 0px 4px 0px rgba(78, 56, 97, 0.2);
    transition: box-shadow .3s ease-in-out;
  }

  &:active {
    box-shadow: 0px 0px 16px 0px rgba(78, 56, 97, 0.2);
    transform: scale(0.95);
    transition: box-shadow .3s ease-in-out;
  }
}

